<script>
import axios from "axios";
import Footer from "@/components/Footer.vue";
import VueRecaptcha from "vue-recaptcha";
import i18n from '../i18n';
export default {
    name: "Jobs",
    components: {
        Footer,
        VueRecaptcha,
    },
    data() {
        return {
            states: {
                AL: 'Alabama',
                AK: 'Alaska',
                AR: 'Arizona',
                CA: 'California',
                NV: 'Nevada'
            },
            sitekey: '6Ldrp84UAAAAAETWXfam5GoCdEVYktGcCVei4iGe',
            form: {
                name: '',
                lastname:'',
                email: '',
                birthday:'',
                phone: '',
                otherQualifications: '',
                remarks: '',
                state: '',
                gender: '',
                weiblich: 'female',
                manlich: 'male',
                drivingLicense: '',
                passengerTransport: '',
                yes: 'yes',
                no: 'no',
                contract: '', 
                fullTime: "Vollzeit",
                partTime: "Teilzeit",
                temporaryHelp: "Aushilfe",
                file: '',
                captcha: ''
            },            
        }
    },
    methods: {
        onSubmit(e) {
            e.preventDefault();
            axios
                .post("http://192.168.0.123/api/vacancy/", {
                    name: this.form.name + " " + this.form.lastname,
                    tel: this.form.phone,
                    birth_date: this.form.birthday,
                    sex: this.form.gender,
                    experience: this.form.state,
                    driver_license_id: this.form.drivingLicense,
                    passenger_transport_license: this.form.passengerTransport,
                    additional_qualification: this.form.otherQualifications,
                    contact_info: this.form.contract,
                    comment: this.form.remarks,
                    resume: this.form.file,
                    captcha: this.form.captcha,
                    email: this.form.email
                    
                }) .then(response => {
                    console.log(response);
                    this.isSuccess = response.data.success ? true : false;
                    this.toast("b-toaster-bottom-left", true);
                })
        },
        handleFileUpload(){
            this.form.file = this.$refs.form.file.files[0];
        },
        onVerify: function (response) {
            this.form.captcha = response
        },
        toast(toaster, append = false) {
            this.$bvToast.toast(
                `Спасибо за оставленную заявку! Мы свяжемся с вами в кратчайшие сроки!`,
                {
                    toaster: toaster,
                    solid: true,
                    appendToast: append
                }
            );
        }
    },
    metaInfo: {
        title: i18n.t('servise.title') ,
        titleTemplate: '%s / Aktiv Taxi Köln',
        htmlAttrs: {
            lang: `${i18n.locale}`,
            amp: true
        }
    }
};

</script>